import React, { useRef } from 'react';
import { formatAmount, getTokenInfo, expandDecimals } from './../../../gmx-test/lib/legacy';
import { separateThousands } from './../../../../components/utils/sepThousands';
import { format } from 'date-fns';
import { msToTimeLeft } from './../../../../components/utils/utils';
import { USD_DECIMALS, formatAmountFree } from './../../lib/legacyOptions';
import { OPTIONS_DECIMALS } from '../../../../components/utils/constants';
import cx from 'classnames';
import Tooltip from './../Tooltip/Tooltip';

const OptionsItem = (props) => {
	const {
		option,
		markPrice,
	} = props;
	
	const curPrice = useRef(expandDecimals(markPrice, 10)).current;

	if (!option) {
		return null;
	}
	
	
	const expirationDate = Date.now() + option.period;
	const logo = require(`../../../../img/icon-${option.market}.svg`).default;
	const isITM = option.isCall
		? curPrice.gte(option.strike)
		: curPrice.lte(option.strike);

	const cls = cx(isITM && 'hlight');
	const isBtnDisabled = !option.liq || option.liq?.eq(0);
	if (isBtnDisabled) {
		return null;
	}
	const btnText = `Buy ${option.isCall ? 'Call' : 'Put'}`;

	const disabledTooltip = 
		<Tooltip
			className="Tooltip_disabled nowrap"
			handle={btnText}
			position="left-bottom"
			enabled={true}
			renderContent={() => {
				return (
					<div className="Tooltip__text">
						There isn’t sufficient <br />liquidity. Please reduce <br />the option size
					</div>
				);
			}}
		/>

	return (
		<tr className={cls}>
			<td>
				<img src={logo} alt={`${option.market} logo`} />
			</td>
			<td>
				{option.type}
			</td>
			<td>
				${formatAmount(option.strike, OPTIONS_DECIMALS, 2, true)}
			</td>
			<td>
				{msToTimeLeft(option.period)}
			</td>
			<td>
				{format(expirationDate, 'dd MMM')}
			</td>
			<td>
				${formatAmount(option.cost, OPTIONS_DECIMALS, 2, true)}
			</td>
			<td>
				${formatAmount(option.price, OPTIONS_DECIMALS, 2, true)}
			</td>
			<td style={{textAlign: 'center'}}>
				{isBtnDisabled
					? disabledTooltip
					: <a href={option.buyUrl ?? '#'} target="_blank" rel="noreferrer"
							className={'btn_inline'}
						>
							{btnText}
						</a>
				}
			</td>
		</tr>
	);
};

export default OptionsItem;